import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { FiMail, FiUser, FiLock, FiEye, FiEyeOff, FiAlertCircle, FiPhone, FiMapPin, FiBriefcase, FiFileText } from 'react-icons/fi';
import { useAuth } from '../../context/AuthContext.js';
import { UserType } from '../../types';
import { companyService } from '../../lib/supabase';

const RegisterCompany: React.FC = () => {
  const navigate = useNavigate();
  const { register, registerCompany } = useAuth();
  
  // بيانات الحساب
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  
  // بيانات الشركة
  const [name, setName] = useState('');
  const [commercialRecord, setCommercialRecord] = useState('');
  const [activity, setActivity] = useState('');
  const [phone, setPhone] = useState('');
  const [website, setWebsite] = useState('');
  const [description, setDescription] = useState('');
  const [location, setLocation] = useState({ latitude: 0, longitude: 0, address: '' });
  
  // حالة النموذج
  const [step, setStep] = useState(1); // خطوة التسجيل (1: بيانات الحساب، 2: بيانات الشركة)
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  
  // التحقق من البريد الإلكتروني
  const isValidEmail = (email: string) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(email);
  };
  
  // التحقق من قوة كلمة المرور
  const isValidPassword = (password: string) => {
    return password.length >= 8;
  };
  
  // تغيير خطوة التسجيل
  const handleNextStep = () => {
    // التحقق من صحة البيانات في الخطوة الأولى
    if (step === 1) {
      if (!email || !password || !confirmPassword) {
        setErrorMessage('جميع الحقول مطلوبة');
        return;
      }
      
      if (!isValidEmail(email)) {
        setErrorMessage('البريد الإلكتروني غير صالح');
        return;
      }
      
      if (!isValidPassword(password)) {
        setErrorMessage('كلمة المرور يجب أن تكون على الأقل 8 أحرف');
        return;
      }
      
      if (password !== confirmPassword) {
        setErrorMessage('كلمات المرور غير متطابقة');
        return;
      }
      
      setErrorMessage(null);
      setStep(2);
    }
  };
  
  // معالجة التسجيل
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (step === 2) {
      // التحقق من صحة البيانات
      if (!validateCompanyData()) return;
      
      setIsLoading(true);
      setErrorMessage(null);
      
      try {
        // استخدام دالة registerCompany من سياق المصادقة
        await registerCompany(
          email,
          password,
          {
            name,
            commercialRecord,
            activity,
            phone,
            website,
            description,
            location
          }
        );
        
        // سيتم التوجيه تلقائياً في دالة registerCompany
      } catch (err: any) {
        console.error('خطأ في تسجيل الشركة:', err);
        
        if (err.message?.includes('email') || err.message?.includes('password')) {
          setStep(1);
        }
        
        setErrorMessage(err.message || 'حدث خطأ أثناء تسجيل الشركة');
      } finally {
        setIsLoading(false);
      }
    } else {
      handleNextStep();
    }
  };
  
  // تبديل عرض كلمة المرور
  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };
  
  // تحديد الموقع الحالي
  const getCurrentLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation({
            ...location,
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          });
        },
        (error) => {
          console.error('خطأ في تحديد الموقع:', error);
          setErrorMessage('فشل في تحديد موقعك الحالي. يرجى إدخاله يدويًا.');
        }
      );
    } else {
      setErrorMessage('متصفحك لا يدعم تحديد المواقع. يرجى إدخال الموقع يدويًا.');
    }
  };
  
  const validateCompanyData = () => {
    // Implement the logic to validate company data
    return true; // Placeholder return, actual implementation needed
  };
  
  const handleNext = () => {
    // Implement the logic to handle moving to the next step
  };
  
  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-primary-50 to-secondary-100 dark:from-secondary-900 dark:to-primary-900 py-12 px-4 sm:px-6 lg:px-8">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="max-w-md w-full bg-white dark:bg-secondary-800 rounded-2xl shadow-xl overflow-hidden"
      >
        <div className="px-6 py-8 sm:p-10">
          <div className="text-center mb-6">
            <h1 className="text-2xl font-bold text-secondary-900 dark:text-white mb-2">تسجيل حساب شركة جديد</h1>
            <p className="text-secondary-600 dark:text-secondary-400">
              {step === 1 ? 'أدخل بيانات حسابك' : 'أكمل بيانات شركتك'}
            </p>
            
            {/* خطوات التسجيل */}
            <div className="flex items-center justify-center mt-4 mb-8">
              <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                step === 1 ? 'bg-primary-600 text-white' : 'bg-primary-200 text-primary-700'
              }`}>
                1
              </div>
              <div className={`h-0.5 w-10 ${
                step === 1 ? 'bg-secondary-300' : 'bg-primary-500'
              }`}></div>
              <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                step === 2 ? 'bg-primary-600 text-white' : 'bg-primary-200 text-primary-700'
              }`}>
                2
              </div>
            </div>
          </div>
          
          {/* رسالة الخطأ */}
          {errorMessage && (
            <div className="bg-red-50 dark:bg-red-900/30 text-red-700 dark:text-red-400 p-3 rounded-lg flex items-start mb-4">
              <FiAlertCircle className="flex-shrink-0 mt-0.5 ml-2" />
              <span>{errorMessage}</span>
            </div>
          )}
          
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* الخطوة الأولى: بيانات الحساب */}
            {step === 1 && (
              <>
                {/* البريد الإلكتروني */}
                <div>
                  <label htmlFor="email" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    البريد الإلكتروني
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiMail className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="email"
                      type="email"
                      autoComplete="email"
                      required
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      className="appearance-none block w-full pl-3 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أدخل بريدك الإلكتروني"
                    />
                  </div>
                </div>
                
                {/* كلمة المرور */}
                <div>
                  <label htmlFor="password" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    كلمة المرور
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiLock className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="password"
                      type={showPassword ? "text" : "password"}
                      required
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      className="appearance-none block w-full pl-10 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أدخل كلمة المرور (8 أحرف على الأقل)"
                    />
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center">
                      <button
                        type="button"
                        onClick={togglePasswordVisibility}
                        className="text-secondary-400 hover:text-secondary-500 dark:hover:text-secondary-300 focus:outline-none"
                      >
                        {showPassword ? <FiEyeOff className="h-5 w-5" /> : <FiEye className="h-5 w-5" />}
                      </button>
                    </div>
                  </div>
                </div>
                
                {/* تأكيد كلمة المرور */}
                <div>
                  <label htmlFor="confirmPassword" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    تأكيد كلمة المرور
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiLock className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="confirmPassword"
                      type={showPassword ? "text" : "password"}
                      required
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      className="appearance-none block w-full pl-3 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أكد كلمة المرور"
                    />
                  </div>
                </div>
                
                {/* زر الانتقال للخطوة التالية */}
                <div>
                  <button
                    type="button"
                    onClick={handleNextStep}
                    className="w-full py-2.5 px-4 border border-transparent rounded-lg shadow-sm text-base font-medium text-white bg-primary-600 hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 transition-colors"
                  >
                    التالي
                  </button>
                </div>
              </>
            )}
            
            {/* الخطوة الثانية: بيانات الشركة */}
            {step === 2 && (
              <>
                {/* اسم الشركة */}
                <div>
                  <label htmlFor="name" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    اسم الشركة
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiBriefcase className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="name"
                      type="text"
                      required
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      className="appearance-none block w-full pl-3 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أدخل اسم الشركة"
                    />
                  </div>
                </div>
                
                {/* السجل التجاري */}
                <div>
                  <label htmlFor="commercialRecord" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    السجل التجاري
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiFileText className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="commercialRecord"
                      type="text"
                      required
                      value={commercialRecord}
                      onChange={(e) => setCommercialRecord(e.target.value)}
                      className="appearance-none block w-full pl-3 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أدخل رقم السجل التجاري"
                    />
                  </div>
                </div>
                
                {/* نشاط الشركة */}
                <div>
                  <label htmlFor="activity" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    نشاط الشركة
                  </label>
                  <select
                    id="activity"
                    required
                    value={activity}
                    onChange={(e) => setActivity(e.target.value)}
                    className="appearance-none block w-full pl-3 pr-3 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                  >
                    <option value="" disabled>اختر نشاط الشركة</option>
                    <option value="هندسية">شركة هندسية</option>
                    <option value="مقاولات">شركة مقاولات</option>
                    <option value="عقارية">شركة عقارية</option>
                    <option value="خدمات مساحية">خدمات مساحية</option>
                    <option value="أخرى">أخرى</option>
                  </select>
                </div>
                
                {/* رقم الهاتف */}
                <div>
                  <label htmlFor="phone" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    رقم الهاتف
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiPhone className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="phone"
                      type="tel"
                      required
                      value={phone}
                      onChange={(e) => setPhone(e.target.value)}
                      className="appearance-none block w-full pl-3 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أدخل رقم هاتف الشركة"
                    />
                  </div>
                </div>
                
                {/* الموقع الإلكتروني (اختياري) */}
                <div>
                  <label htmlFor="website" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    الموقع الإلكتروني (اختياري)
                  </label>
                  <input
                    id="website"
                    type="url"
                    value={website}
                    onChange={(e) => setWebsite(e.target.value)}
                    className="appearance-none block w-full pl-3 pr-3 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                    placeholder="أدخل الموقع الإلكتروني للشركة"
                  />
                </div>
                
                {/* وصف الشركة */}
                <div>
                  <label htmlFor="description" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    وصف الشركة
                  </label>
                  <textarea
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    rows={3}
                    className="appearance-none block w-full pl-3 pr-3 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                    placeholder="أدخل وصفاً مختصراً لشركتك"
                  />
                </div>
                
                {/* العنوان */}
                <div>
                  <label htmlFor="address" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
                    عنوان الشركة
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                      <FiMapPin className="h-5 w-5 text-secondary-400" />
                    </div>
                    <input
                      id="address"
                      type="text"
                      value={location.address}
                      onChange={(e) => setLocation({ ...location, address: e.target.value })}
                      className="appearance-none block w-full pl-3 pr-10 py-2.5 border border-secondary-300 dark:border-secondary-600 rounded-lg text-secondary-900 dark:text-white placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-secondary-700 transition-colors"
                      placeholder="أدخل عنوان الشركة"
                    />
                  </div>
                  <button
                    type="button"
                    onClick={getCurrentLocation}
                    className="mt-2 text-sm text-primary-600 dark:text-primary-400 hover:text-primary-800 dark:hover:text-primary-300 transition-colors"
                  >
                    استخدم الموقع الحالي
                  </button>
                </div>
                
                {/* أزرار التحكم */}
                <div className="flex gap-3">
                  <button
                    type="button"
                    onClick={() => setStep(1)}
                    className="flex-1 py-2.5 px-4 border border-secondary-300 dark:border-secondary-600 rounded-lg shadow-sm text-base font-medium text-secondary-700 dark:text-secondary-300 bg-white dark:bg-secondary-700 hover:bg-secondary-50 dark:hover:bg-secondary-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 transition-colors"
                  >
                    السابق
                  </button>
                  <button
                    type="submit"
                    disabled={isLoading}
                    className={`flex-1 py-2.5 px-4 border border-transparent rounded-lg shadow-sm text-base font-medium text-white bg-primary-600 hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 transition-colors ${isLoading ? 'opacity-70 cursor-not-allowed' : ''}`}
                  >
                    {isLoading ? 'جاري التسجيل...' : 'تسجيل'}
                  </button>
                </div>
              </>
            )}
          </form>
          
          <div className="mt-8 text-center">
            <p className="text-sm text-secondary-600 dark:text-secondary-400">
              لديك حساب بالفعل؟{' '}
              <Link 
                to="/login" 
                className="font-medium text-primary-600 dark:text-primary-400 hover:text-primary-800 dark:hover:text-primary-300 transition-colors"
              >
                سجل دخولك
              </Link>
            </p>
          </div>
        </div>
        
        <div className="px-6 py-4 bg-primary-50 dark:bg-primary-900/20 border-t border-secondary-200 dark:border-secondary-700">
          <p className="text-xs text-center text-secondary-600 dark:text-secondary-400">
            بالتسجيل، أنت توافق على <Link to="/terms" className="underline hover:text-primary-600 dark:hover:text-primary-400">شروط الاستخدام</Link> و <Link to="/privacy" className="underline hover:text-primary-600 dark:hover:text-primary-400">سياسة الخصوصية</Link> الخاصة بنا.
          </p>
        </div>
      </motion.div>
    </div>
  );
};

export default RegisterCompany; 