import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext.js';

// استيراد الصفحات
import Login from './pages/Login';
import Home from './pages/Home';
import RegisterCompany from './pages/auth/RegisterCompany.tsx';
import RegisterSurveyor from './pages/auth/RegisterSurveyor.tsx';
import ForgotPassword from './pages/auth/ForgotPassword';

// مكون صفحة تحت الإنشاء
const UnderConstruction = () => {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-primary-50 to-secondary-100 p-4">
      <div className="max-w-md w-full bg-white rounded-2xl shadow-xl overflow-hidden p-8 text-center">
        <h1 className="text-3xl font-bold text-secondary-900 mb-4">
          الصفحة تحت الإنشاء
        </h1>
        <p className="text-xl text-secondary-600 mb-8">
          هذه الصفحة قيد التطوير، سيتم إطلاقها قريباً
        </p>
        <Link to="/" className="px-6 py-3 bg-primary-600 hover:bg-primary-700 text-white font-medium rounded-lg shadow-lg hover:shadow-xl transition-all">
          العودة للصفحة الرئيسية
        </Link>
      </div>
    </div>
  );
};

// مكون صفحة نجاح التسجيل
const RegistrationSuccess = () => {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-primary-50 to-secondary-100 p-4">
      <div className="max-w-md w-full bg-white rounded-2xl shadow-xl overflow-hidden p-8 text-center">
        <div className="w-20 h-20 bg-green-100 text-green-600 rounded-full flex items-center justify-center mx-auto mb-4">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-10 w-10" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
          </svg>
        </div>
        <h1 className="text-3xl font-bold text-secondary-900 mb-4">
          تم التسجيل بنجاح!
        </h1>
        <p className="text-xl text-secondary-600 mb-8">
          شكراً لتسجيلك في منصتنا. يمكنك الآن تسجيل الدخول للوصول إلى لوحة التحكم الخاصة بك.
        </p>
        <Link to="/login" className="px-6 py-3 bg-primary-600 hover:bg-primary-700 text-white font-medium rounded-lg shadow-lg hover:shadow-xl transition-all">
          تسجيل الدخول
        </Link>
      </div>
    </div>
  );
};

const App = () => {
  return (
    <Router>
      <AuthProvider>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register-company" element={<RegisterCompany />} />
          <Route path="/register-surveyor" element={<RegisterSurveyor />} />
          <Route path="/registration-success" element={<RegistrationSuccess />} />
          <Route path="/dashboard/company" element={<UnderConstruction />} />
          <Route path="/dashboard/surveyor" element={<UnderConstruction />} />
          <Route path="/forgot-password" element={<ForgotPassword />} />
          <Route path="/reset-password" element={<UnderConstruction />} />
          <Route path="/terms" element={<UnderConstruction />} />
          <Route path="/privacy" element={<UnderConstruction />} />
          <Route path="/pricing" element={<UnderConstruction />} />
          <Route path="*" element={<Home />} />
        </Routes>
      </AuthProvider>
    </Router>
  );
};

export default App; 