import { createClient, SupabaseClient } from '@supabase/supabase-js';
import { UserType, Surveyor, Company, Project, FileAttachment } from '../types';

// تكوين Supabase
// ملاحظة: استخدم بيانات وهمية مؤقتة للتطوير والعرض
// في الإنتاج، استبدل هذه القيم بمتغيرات البيئة الحقيقية
const supabaseUrl = process.env.REACT_APP_SUPABASE_URL || 'https://example.supabase.co';
const supabaseKey = process.env.REACT_APP_SUPABASE_ANON_KEY || 'example-anon-key';

// إنشاء عميل Supabase
export const supabaseClient: SupabaseClient = createClient(supabaseUrl, supabaseKey);

// خدمات المصادقة
export const authService = {
  // تسجيل دخول المستخدم
  login: async (email: string, password: string) => {
    const { data, error } = await supabaseClient.auth.signInWithPassword({
      email,
      password,
    });
    
    if (error) throw error;
    return data;
  },
  
  // تسجيل مستخدم جديد
  register: async (email: string, password: string, userData: any = {}) => {
    const { data, error } = await supabaseClient.auth.signUp({
      email,
      password,
      options: {
        data: {
          ...userData
        }
      }
    });
    
    if (error) throw error;
    return data;
  },
  
  // تسجيل خروج المستخدم
  logout: async () => {
    const { error } = await supabaseClient.auth.signOut();
    if (error) throw error;
  },
  
  // الحصول على المستخدم الحالي
  getCurrentUser: async () => {
    const { data, error } = await supabaseClient.auth.getUser();
    if (error) throw error;
    return data;
  },
  
  // تحديث بيانات المستخدم
  updateUserData: async (userData: any) => {
    const { data, error } = await supabaseClient.auth.updateUser({
      data: userData
    });
    
    if (error) throw error;
    return data;
  }
};

// خدمات المساحين
export const surveyorService = {
  // إضافة مساح جديد
  addSurveyor: async (surveyorData: Partial<Surveyor>) => {
    const { data, error } = await supabaseClient
      .from('surveyors')
      .insert([surveyorData])
      .select();
    
    if (error) throw error;
    return data?.[0];
  },
  
  // الحصول على مساح بواسطة المعرف
  getSurveyorById: async (id: string) => {
    const { data, error } = await supabaseClient
      .from('surveyors')
      .select('*')
      .eq('id', id)
      .single();
    
    if (error) throw error;
    return data;
  },
  
  // الحصول على مساح بواسطة معرف المستخدم
  getSurveyorByUserId: async (userId: string) => {
    const { data, error } = await supabaseClient
      .from('surveyors')
      .select('*')
      .eq('user_id', userId)
      .single();
    
    if (error) throw error;
    return data;
  },
  
  // تحديث بيانات المساح
  updateSurveyor: async (id: string, surveyorData: Partial<Surveyor>) => {
    const { data, error } = await supabaseClient
      .from('surveyors')
      .update(surveyorData)
      .eq('id', id)
      .select();
    
    if (error) throw error;
    return data?.[0];
  },
  
  // البحث عن المساحين
  searchSurveyors: async (filters: {
    location?: string;
    specialization?: string;
    available?: boolean;
    experience_years?: number;
  }) => {
    let query = supabaseClient.from('surveyors').select('*');
    
    // تطبيق الفلاتر إذا كانت موجودة
    if (filters.specialization) {
      query = query.contains('specialization', [filters.specialization]);
    }
    
    if (filters.available !== undefined) {
      query = query.eq('available', filters.available);
    }
    
    if (filters.experience_years) {
      query = query.gte('experience_years', filters.experience_years);
    }
    
    // يمكن إضافة المزيد من الفلاتر هنا
    
    const { data, error } = await query;
    
    if (error) throw error;
    return data;
  }
};

