import React, { createContext, useContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { supabaseClient, authService } from '../lib/supabase.ts';

// إنشاء سياق المصادقة
const AuthContext = createContext();

// مزود سياق المصادقة
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [role, setRole] = useState(null); 
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  // التحقق من حالة المصادقة عند تحميل التطبيق
  useEffect(() => {
    const initializeAuth = async () => {
      try {
        setLoading(true);
        
        // الحصول على المستخدم الحالي (إن وجد)
        const { data: currentUser, error } = await supabaseClient.auth.getUser();
        
        if (error) {
          console.error('خطأ في التحقق من حالة المصادقة:', error);
          setUser(null);
          setRole(null);
          return;
        }

        if (currentUser.user) {
          setUser(currentUser.user);
          
          // الحصول على دور المستخدم من قاعدة البيانات
          const { data: surveyorData } = await supabaseClient
            .from('surveyors')
            .select('*')
            .eq('user_id', currentUser.user.id)
            .single();
            
          const { data: companyData } = await supabaseClient
            .from('companies')
            .select('*')
            .eq('user_id', currentUser.user.id)
            .single();
          
          if (surveyorData) {
            setRole('surveyor');
          } else if (companyData) {
            setRole('company');
          }
        } else {
          setUser(null);
          setRole(null);
        }
      } catch (error) {
        console.error('خطأ في تهيئة حالة المصادقة:', error);
      } finally {
        setLoading(false);
      }
    };

    // الاشتراك في تغييرات حالة المصادقة
    const { data: authListener } = supabaseClient.auth.onAuthStateChange(
      async (event, session) => {
        if (session?.user) {
          setUser(session.user);
          
          // الحصول على دور المستخدم من قاعدة البيانات
          const { data: surveyorData } = await supabaseClient
            .from('surveyors')
            .select('*')
            .eq('user_id', session.user.id)
            .single();
            
          const { data: companyData } = await supabaseClient
            .from('companies')
            .select('*')
            .eq('user_id', session.user.id)
            .single();
          
          if (surveyorData) {
            setRole('surveyor');
          } else if (companyData) {
            setRole('company');
          }
        } else {
          setUser(null);
          setRole(null);
        }
        
        setLoading(false);
      }
    );

    initializeAuth();

    // تنظيف المستمع عند إزالة المكون
    return () => {
      if (authListener && authListener.subscription) {
        authListener.subscription.unsubscribe();
      }
    };
  }, []);

  // دالة تسجيل الدخول
  const login = async (email, password) => {
    try {
      const { data, error } = await authService.login(email, password);
      
      if (error) throw error;
      
      if (data.user) {
        setUser(data.user);
        
        // تحديد دور المستخدم وتوجيهه إلى لوحة التحكم المناسبة
        const { data: surveyorData } = await supabaseClient
          .from('surveyors')
          .select('*')
          .eq('user_id', data.user.id)
          .single();
          
        const { data: companyData } = await supabaseClient
          .from('companies')
          .select('*')
          .eq('user_id', data.user.id)
          .single();
        
        if (surveyorData) {
          setRole('surveyor');
          navigate('/dashboard/surveyor');
        } else if (companyData) {
          setRole('company');
          navigate('/dashboard/company');
        }
      }
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في تسجيل الدخول:', error);
      throw error;
    }
  };

  // دالة تسجيل الخروج
  const logout = async () => {
    try {
      const { error } = await authService.logout();
      
      if (error) throw error;
      
      setUser(null);
      setRole(null);
      navigate('/login');
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في تسجيل الخروج:', error);
      throw error;
    }
  };

  // تسجيل مستخدم جديد (شركة)
  const registerCompany = async (email, password, companyData) => {
    try {
      const { data, error } = await authService.register(email, password);
      
      if (error) throw error;

      if (data.user) {
        // إضافة بيانات الشركة إلى جدول الشركات
        const { error: companyError } = await supabaseClient
          .from('companies')
          .insert({
            user_id: data.user.id,
            name: companyData.name,
            commercial_record: companyData.commercialRecord,
            activity: companyData.activity,
            phone: companyData.phone,
            website: companyData.website || null,
            description: companyData.description,
            location: companyData.location
          });

        if (companyError) throw companyError;

        setUser(data.user);
        setRole('company');
        navigate('/dashboard/company');
      }
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في تسجيل الشركة:', error);
      throw error;
    }
  };

  // تسجيل مستخدم جديد (مستطلع)
  const registerSurveyor = async (email, password, surveyorData) => {
    try {
      const { data, error } = await authService.register(email, password);
      
      if (error) throw error;

      if (data.user) {
        // إضافة بيانات المستطلع إلى جدول المستطلعين
        const { error: surveyorError } = await supabaseClient
          .from('surveyors')
          .insert({
            user_id: data.user.id,
            first_name: surveyorData.firstName,
            last_name: surveyorData.lastName,
            national_id: surveyorData.nationalId,
            phone: surveyorData.phone,
            birth_date: surveyorData.birthDate,
            gender: surveyorData.gender,
            education_level: surveyorData.educationLevel,
            location: surveyorData.location,
            experience_years: surveyorData.experienceYears,
            bio: surveyorData.bio,
            specializations: surveyorData.specializations,
            equipment: surveyorData.equipment
          });

        if (surveyorError) throw surveyorError;

        setUser(data.user);
        setRole('surveyor');
        navigate('/dashboard/surveyor');
      }
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في تسجيل المستطلع:', error);
      throw error;
    }
  };

  // تحديث بيانات المستخدم
  const updateUserProfile = async (userData) => {
    try {
      if (!user) throw new Error('لا يوجد مستخدم مسجل الدخول');

      if (role === 'company') {
        const { error } = await supabaseClient
          .from('companies')
          .update(userData)
          .eq('user_id', user.id);
          
        if (error) throw error;
      } else if (role === 'surveyor') {
        const { error } = await supabaseClient
          .from('surveyors')
          .update(userData)
          .eq('user_id', user.id);
          
        if (error) throw error;
      }
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في تحديث بيانات المستخدم:', error);
      throw error;
    }
  };

  // تغيير كلمة المرور
  const changePassword = async (newPassword) => {
    try {
      const { error } = await authService.updatePassword(newPassword);
      
      if (error) throw error;
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في تغيير كلمة المرور:', error);
      throw error;
    }
  };

  // إعادة تعيين كلمة المرور
  const resetPassword = async (email) => {
    try {
      const { error } = await authService.resetPassword(email);
      
      if (error) throw error;
      
      return { success: true };
    } catch (error) {
      console.error('خطأ في إعادة تعيين كلمة المرور:', error);
      throw error;
    }
  };

  // القيمة التي سيتم توفيرها للمكونات
  const value = {
    user,
    role,
    loading,
    login,
    logout,
    registerCompany,
    registerSurveyor,
    updateUserProfile,
    changePassword,
    resetPassword,
    isAuthenticated: !!user
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

// هوك الوصول إلى سياق المصادقة
export const useAuth = () => {
  const context = useContext(AuthContext);
  
  if (!context) {
    throw new Error('يجب استخدام useAuth داخل AuthProvider');
  }
  
  return context;
};

// هوك حماية المسارات المقيدة
export const useProtectedRoute = (allowedRoles = []) => {
  const { user, role, loading } = useAuth();
  const navigate = useNavigate();
  
  useEffect(() => {
    if (!loading && !user) {
      navigate('/login');
    } else if (!loading && user && allowedRoles.length > 0 && !allowedRoles.includes(role)) {
      navigate('/');
    }
  }, [user, role, loading, navigate, allowedRoles]);
  
  return { isAuthenticated: !!user, role, loading };
};

export default AuthContext; 